import React, { Component } from "react";
import { Button, Progress } from "semantic-ui-react";

export default class ProjectProgress extends Component {
  state = { percent: (this.props.progress / this.props.goal) * 100 };

  render() {
    let progress;
    if (this.props.progress === null) {
      progress = 0;
    } else {
      progress = (this.props.progress / this.props.goal) * 100;
    }

    return (
      <div>
        <Progress percent={this.state.progress} indicating />
      </div>
    );
  }
}
